<template>
  <v-navigation-drawer v-model="Customizer_drawer" app temporary :right="!$vuetify.rtl">
    <!---Theme Color -->
    <div class="px-4 py-4">
      <a  @click="go_login"> 登录/注册</a>
    </div>
    <div class="px-4 py-4">
      <a  @click="go_userinfo"> 个人资料</a>
    </div>
    <v-divider></v-divider>
    <div class="px-4 py-4">
      <a href="/help/help/readme.html"> 帮助中心</a>
    </div>
    <v-divider></v-divider>
    <div class="px-4 py-4">
      <a href="https://www.wkj.link/register?invit=ZXCHBW"> 玩客家交易所</a>
    </div>
    <v-divider></v-divider>
  </v-navigation-drawer>
</template>

<script>
import Proxyable from "vuetify/lib/mixins/proxyable";
export default {
  name: "Customizer",
  mixins: [Proxyable],
  data: () => ({
    right: false,
    SidebarColor: "",
    SidebarColors: [
      "white",
      "#2b2b2b",
      "rgb(44, 59, 164)",
      "rgb(96, 44, 164)",
      "rgb(151, 210, 219)",
      "rgb(77, 86, 100)"
    ],
    themecolor: "#1e88e5",
    themecolors: [
      "#1e88e6",
      "#21c1d6",
      "#fc4b6c",
      "#563dea",
      "#9C27b0",
      "#ff9800"
    ]
  }),
  computed: {
    Customizer_drawer: {
      get() {
        return this.$store.state.Customizer_drawer;
      },
      set(val) {
        this.$store.commit("SET_CUSTOMIZER_DRAWER", val);
      }
    },
    setSidebarColor: {
      get() {
        return this.$store.state.SidebarColor;
      },
      set(val) {
        this.$store.commit("SET_SIDEBAR_COLOR", val);
      }
    }
  },
  methods:{
    go_userinfo(){
        this.$router.push({path:"/user/userinfo"});
    },
    go_login(){
      this.$router.push({path:"/user/login"});
    },
  },
  watch: {
    themecolor(val) {
      (this.$vuetify.theme.themes[
        this.isDark ? "dark" : "light"
      ].primary = val),
        (this.$vuetify.theme.themes[
          this.isLight ? "light" : "dark"
        ].primary = val);
    },
    setSidebarColor(val) {
      this.$store.commit("SET_SIDEBAR_COLOR", val);
    }
  },

};
</script>

<style lang="scss">
   .v-application .v-item-group .v-sidebar_active {
        border:1px solid black !important;
    }
</style>